import React from 'react'
import { Typography, Card } from 'antd'
import ModalImage from "react-modal-image";
import {Link} from 'gatsby';
import LandingLayout from '@layouts/LandingLayout'


export default () => (
  <LandingLayout activeTab = '2'>
    <div className='flex flex-col items-start'>
    <Typography.Title className='text-primary-color'>
        Dev Notes: First Release
        </Typography.Title> 
        <Typography.Title level={4} className='text-gray-600'>
     Aug 6, 2020 <br/> This is the first release for this app. I intended this app to be a simple 
     experement but I hope that it will be impactful. The inspiration for this app's development came about
     while using a messaging app that claimed to use high security and encryption to keep messages private 
     with nothing more than their word to show for it. This app looks to fix that problem by letting the user
     use their own numbers to generate keys, access their keys and generate keys using timing data on certain
     button presses. <br/>
     </Typography.Title>
     <Typography.Title level={3} className='text-primary-color'>
         Known Bugs:
     </Typography.Title>
     <Typography.Title level={4} className='text-gray-600'>
         Share button does not work on iPad.<br/>         
     </Typography.Title>
     <Typography.Title level={3} className='text-primary-color'>
         Features in Progress:
     </Typography.Title>
     <Typography.Title level={4} className='text-gray-600'>
         Fix iPad share button.<br/>
         Make message text field multiline <br/>
         Add contact button and flesh out info screen         
     </Typography.Title>
     <Typography.Title level={3} className='text-primary-color'>
        Planned changes:
     </Typography.Title>
     <Typography.Title level={4} className='text-gray-600'>
         Mathematical optimizations to be made to the encryption process that affects *very* long messages<br/>
         Make the message input section accept files uploads and images<br/>
         Add QR codes (still don't know if this is feasible given formatted message size)<br/>
     </Typography.Title>
    </div>
  </LandingLayout>
)
